#name {
  position: relative;
  font-size: 25px;
  font-family: "Anton";
  padding-top: 50px;
  font-weight: 100;
  letter-spacing: 5px;
  color: orange;
  list-style-type: none;
  text-transform: uppercase;
  line-height: normal;
  padding-bottom: 10px;
  
}
#photo {
  position: relative;
  float: right;
  right: -1600px;
  border: 3px solid orange;
  border-radius: 5px;
  -webkit-animation: photo 0.5s forwards;
  -webkit-animation-delay: 1s;
  animation: photo 0.5s forwards;
  animation-delay: 0s;
}
@-webkit-keyframes photo {
  100% {
    right: 0;
  }
}

@keyframes photo {
  100% {
    right: 0;
  }
}

